import React, { Component } from 'react';
import { Container, Row, Col, Button, Form, FormGroup, Input } from 'reactstrap';

export default class LandingPage extends Component {
    constructor(props) {
        super(props)
        this.checkCookie = this.checkCookie.bind(this)
    }

    componentDidMount() {
        this.checkCookie()
    }

    checkCookie() {
        const split = document.cookie.split(';')
        const allCookies = []
        const cookieNames = []

        return new Promise((resolve, reject) => {

            split.map((item, index) => {
                let name = item.slice(1,7)
                let value = item.slice(8)
                cookieNames.push(name)
                allCookies.push({[name]: value})
            })
            
            switch(cookieNames.includes('sessid')){
                case true:
                  let index = cookieNames.indexOf('sessid')
                  let cookie = allCookies[index].sessid
                    this.user_auth(cookie)
                    .then((response) =>{
                        resolve(response)
                    })
                    break;
                case false:
                      resolve(false)
                    break;
                default:
                    console.log('Whoops... Something broke..')
            }
            
          })
    }

    user_auth(cookie) {
        console.log(cookie, 'user auth before fetch')
        fetch('/user_auth', {
            method: 'POST',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                sessid: cookie,
            })

        })
            .then((response) => response.json())
            .then((responseJson) => {
                console.log(responseJson, 'responseJson')
                if (responseJson.passCheck === undefined) {
                    this.props.history.push('/')
                } else {
                    console.log(responseJson.passCheck, 'incorrect pass')
                }
            })
            .catch((error) => {
                console.error(error);
            });
    }

    render() {
        return(
            <Container>
                <h1>Home Page</h1>
            </Container>
        )
    }
}


